import Slider from 'infinite-react-carousel/lib/carousel/slider';
import React, { useEffect, useState } from 'react';
import person1 from '../images/home/1.png';
import person2 from '../images/home/2.png';
import person3 from '../images/home/3.png';
import styles from '../styles/HomeSlider.module.css';
import SliderCard from './SliderCard';

function HomeSlider() {
  const { container } = styles;
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    checkIsMobile();
    window.addEventListener('resize', () => {
      checkIsMobile();
    });
  }, []);

  const checkIsMobile = () => {
    if (window.innerWidth <= 764) {
      setIsMobile(true);
    } else {
      setIsMobile(false);
    }
  };

  return (
    <Slider
      centerMode
      initialSlide={1}
      className={container}
      arrows={!isMobile}
      shift='50'
      centerPadding={30}
    >
      {[
        <SliderCard
          key={person1}
          img={person1}
          title='Viaje com segurana!'
          text='Temos um sistema de rastreamento exclusivo que te garante segurana durante as viagens. Sua localizao em tempo real pode ser compartilhada atravs de um Link.'
        />,
        <SliderCard
          key={person2}
          img={person2}
          title='Indique motoristas!'
          text='Voc conhece algum motorista de aplicativo? Voc pode enviar para ele o Link de download do app de motoristas e receber bonificaes por ter feito a indicao!'
        />,
        <SliderCard
          key={person3}
          img={person3}
          title='Saiba mais sobre a Lev!'
          text='Conhea toda a nossa histria, de onde surgiu a ideia, como foi nosso comeo e como nos tornamos
            o que somos hoje.'
        />,
      ]}
    </Slider>
  );
}

export default HomeSlider;
